#shop {
    display: flex;
    flex-direction: column;
}

#shop .top-panel {
    padding: 50px 100px;
}

#shop .top-panel .title {
    color: black;
    font-size: 40px;
    font-weight: 700;

    margin-bottom: 20px;
}

#shop .top-panel .text {
    color: rgba(0, 0, 0, 0.5);
    font-size: 18px;
    font-weight: 400;
    line-height: 30px;

    width: 70%;
}

#shop .filters {
    display: flex;
    flex-direction: row;

    padding: 0px 100px;
}

#shop .filters .field {
    color: black;
    font-size: 15px;
    font-weight: 400;

    padding: 8px;
    margin-right: 20px;

    width: 250px;

    border: 1px solid lightgray;
    outline: none;
}

#shop .products {
    width: 100%;

    display: grid;
    /*grid-template-columns: auto auto auto auto;*/
    grid-template-columns: repeat(4, 1fr);
    gap: 60px 80px;

    padding: 50px 100px;

    margin-bottom: 30px;
}

#shop .products .product {
    display: flex;
    flex-direction: column;

    text-decoration: none;
}

#shop .products .product .image {
    width: 100%;
    height: 230px;
    object-fit: contain;

    padding: 30px 0px;
    margin-bottom: 10px;

    background-color: rgba(0, 0, 0, 0.03);
}

#shop .products .product .name {
    color: black;
    font-size: 20px;
    font-weight: 700;
}

#shop .products .product .description {
    color: rgba(0, 0, 0, 0.4);
    font-size: 15px;
    font-weight: 400;

    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}

#shop .products .product .panel {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;

    margin-top: 15px;
}

#shop .products .product .panel .price {
    color: black;
    font-size: 20px;
    font-weight: 400;
}

#shop .products .product .panel .button-filled {
    font-size: 13px;
    font-weight: 400;
    padding: 6px 15px;
}

#shop .empty-message {
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    padding: 75px 100px;

    color: black;
    font-size: 20px;
    font-weight: 400;
}

#shop .button-filled {
    align-self: center;
}