#product {
    width: 100%;
}

#product .content {
    width: 100%;

    display: flex;
    flex-direction: row;

    padding: 50px 100px;
}

#product .content .image {
    width: 150px;

    margin: 0px 100px 0px 50px;
}

#product .content .info-panel {
    flex: 1;
}

#product .content .info-panel .name {
    color: black;
    font-size: 40px;
    font-weight: 700;

    margin-bottom: 20px;
}

#product .content .info-panel .description {
    color: rgba(0, 0, 0, 0.5);
    font-size: 16px;
    font-weight: 400;
    line-height: 30px;
}